import Mock from 'mockjs' //mock.js
const Random = Mock.Random  //Mock.Random 
const dataList = [] // 

for(let i = 0; i < 30; i++){
    const template = {
        'boolean' : Random.boolean, // 
        'Natural': Random.natural(1, 10), // 1100
        'Character': Random.character(), // ,
        'String': Random.string(2, 10), // 210
        'Range': Random.range(0, 10, 2), // 
        'Date': Random.date(), // ,
        'Paragraph': Random.paragraph(2, 5), // 25
        'Name': Random.name(), // 
        'Url': Random.url(), // web
        'Address': Random.province() // 
    }
    dataList.push(template);
}
// console.log(dataList)

//list()
Mock.mock('/api/list','post',( params ) => {
    var info = JSON.parse(params.body)
    var [index, size, total] = [info.pageIndex, info.pageSize, dataList.length]
    var len = total / size
    var totalPages = len - parseInt(len) > 0 ? parseInt(len) + 1 : len
    var newDataList = dataList.slice(index * size, (index + 1) * size)

    return {
        'code'    : '0',
        'message' : 'success',
        'data'    : {
            'pageIndex'  : index,
            'pageSize'   : size,
            'rows'       : newDataList,
            'total'      : total,
            'totalPages' : totalPages
        }
    }
})

//mock
