import Vue from 'vue'
import App from './App.vue'
import Router from 'vue-router' //
// import VueResource from 'vue-resource'  //
import { routes } from './router/routes'   //router
import axios from 'axios'
import ElementUI, { Header } from 'element-ui' //element-Ui
import Mock from './mock' //mock.js
import Icon from 'vue-awesome/components/Icon'
//store
import store from './store/store'
//
import VueLazyload from 'vue-lazyload' 

//css
import './assets/css/reset.css' //css reset
import './assets/css/common.css' //
import './assets/css/nav.css' //
import './assets/css/icon.css' //iconmoon
import './assets/css/main-versions.css'
import './assets/css/animations-3.css' //
import 'element-ui/lib/theme-chalk/index.css' //element ui 

//element
import 'element-ui/lib/theme-chalk/display.css' 

// -- scss
import './assets/style/total.scss' 

//js
import './assets/js/jquery-3.2.1.min.js'
import './assets/js/jquery-ui.min.js'

//router
Vue.use(Router)
//element ui
Vue.use(ElementUI)
//icon
Vue.component('icon',Icon)
//
Vue.use(VueLazyload,{
  preLoad: 1.3, //
  attempt:2, //
  listenEvents:['scroll','resize','animationend'], // - 
  error:'../../static/icons/err.gif', //
  loading:'../../static/icons/loading.gif' //
})

// axios$axios(axios)
// axios.defaults.baseURL = 'localhost:8080'
Vue.prototype.$http = axios

//
const router = new Router({
  routes,
  mode: 'history' // hash
})

// /login page
router.beforeEach((to, from, next) => {
  //localstorage value
  const isLogin = localStorage.getItem('token') == 'ImLogin'

  /* title to */
  if(to.meta.title){
    document.title = to.meta.title
  }
  next();

//
  if( isLogin ){
    next(); //
  } else {
    if( to.path !== '/login') ///login
      next('/login'); //login page
    else
      next(); 
  }
});

//mixin

Vue.config.productionTip = false

new Vue({
  router, //
  store, //vuexstore $store
  render: h => h(App),
}).$mount('#app')
